.sidebar-profile{list-style:none}.sidebar-profile li{position:relative;transition:all 0.3s ease}.sidebar-profile li a{padding:1rem;font-size:1rem;cursor:pointer;transition:all 0.3s ease}.sidebar-profile li .icon{width:25px;display:inline-block}.sidebar-profile li+li a{border-top:1px solid #e1e1e1}.sidebar-profile li:hover{background:#fff3e8}.sidebar-profile li:hover a{border-color:#fff3e8}.sidebar-profile li:hover a>span{transition:all 0.3s ease}.sidebar-profile li:hover a>span:last-child{margin-left:15px}.sidebar-profile li:hover+li a{border-color:#fff3e8}.sidebar-profile li.active a{color:#fd7e14}.teacher__detail{border-radius:0.42rem;overflow:hidden}.card-visual{margin-left:auto;margin-right:auto;box-shadow:0px 14px 28px 0px rgba(0,0,0,0.15);border-radius:100px}.card-visual .visual-name{position:absolute;bottom:60px;left:30px;font-size:20px;color:rgba(159,203,255,0.45);font-weight:bold;letter-spacing:1px;text-transform:uppercase;text-shadow:4px 4px 4px rgba(0,0,0,0.3)}.card-visual .visual-number{position:absolute;bottom:30px;left:30px;font-size:20px;color:rgba(159,203,255,0.45);text-shadow:4px 4px 4px rgba(0,0,0,0.3);font-weight:bold;letter-spacing:1.5px;text-transform:uppercase}.card-visual .visual-bank{position:absolute;top:25px;right:30px;font-size:16px;color:#ffffff;font-weight:bold;letter-spacing:2px;text-transform:uppercase;text-shadow:4px 4px 4px rgba(0,0,0,0.3)}@media screen and (max-width: 580px){.card-visual .visual-name{bottom:40px;left:20px;font-size:14px}.card-visual .visual-number{bottom:20px;left:20px;font-size:14px}.card-visual .visual-bank{font-size:12px;top:15px;right:18px}}
